html, body {
	font: 14px/1.21 'Helvetica Neue', arial, sans-serif;
	font-weight: 400;
}

h1 {
	text-align: center;
}

.fullpageImage { 
  /* The image used */
  background-image: url("https://i.imgur.com/gskHA7H.jpg");

  /* Full height */
  height: 100vh; 

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.page-title {
  color: white;
}

.carousel-item > img {
  margin:auto;
}

.jumbotron {
  background-color:transparent !important; 
}

.card-body {
  background-color:rgb(89, 98, 105) !important; 
}


